import emojiRegex from "emoji-regex";

const regex = emojiRegex();

export function renderEmojis(content) {
  const newContent = content.replace(regex, (match) => {
      let hexCode = "";
      for (const codePoint of match) {
          hexCode += codePoint.codePointAt(0).toString(16) + "-";
      }
      hexCode = hexCode.slice(0, -1);
      return `<img class="emoji" alt="${match}" src="img/emoji/${hexCode}.png" />`;
  });
  return newContent;
}
